import React from 'react';
import ImagePage from '../ImagePage';

import image1 from './1.jpg';
import image2 from './2.jpg';
import image3 from './3.jpg';

const data = [
    {
        title: '',
        image: image1,
    },
    {
        title: '',
        image: image2
    },
    {
        title: '',
        image: image3
    },
];

const career = () => (
	<ImagePage data={data}/>
);
career.path = '/career';

export default career;



