import React, {Component} from 'react';
import {
  Grid,
  Row,
  Col,
} from 'react-bootstrap';
import {PageTitle} from '../../components';
import './services.css';

const services = [
    {
        title: '',
        content: [
            '',
            '',
            '',
            '',
            '',
        ]
    },
    {
        title: '',
        content: [
            '',
            '',
            ''
        ]
    },
    {
        title: '',
        content: [
            '',
            '',
            '',
        ]
    },
    {
        title: '',
        content: [
            '',
            '',
            ''
        ]
    },
    {
        title: '',
        content: [
            '',
            ','
        ]
    },
    {
        title: '',
        content: [
            '',
            '',
            ''
        ]
    }
];

class IndexService extends Component {
    render() {
        return (
                <Grid>
                    <PageTitle
                        title={''}
                        description={'  '}
                    />
                    <ServiceList data={services}/>
                </Grid>
        );
    }
}

const ServiceList = ({data}) => (
    <Row>
        {data.map((item, index) => (
            <ServiceItem key={index} data={item} index={index}/>
        ))}
    </Row>
);

const ServiceItem = ({data, index}) => (
    <Col lg={2} sm={4} xs={6} className="service-item">
        <div className={`service-item-icon service-item-icon${index + 1}`}>
        </div>
        <div className="service-item-title">
            {data.title}
        </div>
        <div className="service-item-content">
            <ul>
                {data.content.map((item, index) => (
                    <li key={index}>{item}</li>
                ))}
            </ul>
        </div>
    </Col>
);


export default IndexService;



